.statistics {
  width: 1200px;
  margin:  0 auto;
    background-color: #fff;
    padding-top: 50px;
    text-align: center;
    padding-bottom: 60px;
    border-radius: 5px;
    overflow: hidden;
  }
  
  .title {
    color: grey;
    text-transform: uppercase;
    margin-top: 0;
    margin-bottom: 50px;
  }
  
  .statList {
    display: flex;
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  .item {
    padding: 20px 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: calc(100% / 5);
    color: #fff;
  }
  
  .label {
    font-size: 14px;
  }
  
  .percentage {
    margin-top: 5px;
    font-size: 20px;
  }